
import { ImageSize, ItemMode, Price } from "@/interface";
import { convertFlickrImage } from "@/mixins/generalMixin";
import { defineComponent, PropType } from "vue";

export default defineComponent({
  props: {
    orderList: {
      type: Object as PropType<Price[]>,
      required: true,
    },
  },
  emits: ["showItemDetail", "updateItemPriceToOrder"],
  data() {
    return {
      ImageSize: ImageSize,
      itemMode: ItemMode,
    };
  },
  methods: {
    showItemDetail(id: number) {
      this.$emit("showItemDetail", id);
    },
    updateItemPriceToOrder(itemPrice: Price, qty: number) {
      this.$emit("updateItemPriceToOrder", { itemPrice, qty });
    },
    convertFlickrImage(imageLink: string, size: ImageSize | null) {
      return convertFlickrImage(imageLink, size);
    },
  },
});
